@font-face {
  font-family: 'Montserrat';
  src: url(/react-norris/static/media/Montserrat-Regular.9c460951.ttf);
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  --bg-color: #eceff1;
  --primary-text-color: #212121;
  --error-color: #f4511e;
}

:root {
  --btn-bg-color: #fbfbfb;
  --btn-bg-hovered-color: #f3f3f3;
  --btn-outline-color: #d2d2d2;
  --btn-box-shadow-color: #e2e2e2;
}

.App {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: var(--bg-color);
}

.App-has-error {
  color: var(--error-color);
}

.App-setting-bar {
  position: absolute;
  top: 0;
  right: 0;
  padding: 15px;
}

.App-button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px 10px;
  margin: 0;
  color: var(--primary-text-color);
  background: #fbfbfb;
  background: var(--btn-bg-color);
  cursor: pointer;
  font-size: 0.85rem;
  text-align: center;
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  border-radius: 4px;
  box-shadow: 0px .5px 3px #e2e2e2;
  box-shadow: 0px .5px 3px var(--btn-box-shadow-color);
  -webkit-transition: background 250ms ease-in-out;
  transition: background 250ms ease-in-out;
}

.App-button:hover,
.App-button:focus {
  background: #f3f3f3;
  background: var(--btn-bg-hovered-color);
}

.App-button:focus {
  outline: 1px dotted #d2d2d2;
  outline: 1px dotted var(--btn-outline-color);
  outline-offset: -4px;
}

.App-button-icon {
  width: 20px;
  height: 20px;
}

.App-hero-form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.App-hero-form > * {
  height: 30px;
  font-size: 0.85rem;
}

.App-hero-input {
  width: 100%;
  -webkit-flex-grow: 3;
          flex-grow: 3;
  margin-right: 12px;
  padding: 10px;
  vertical-align: middle;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #ffffff;
  border: 1px solid #dddddd;
}

.App-hero-button {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

@media only screen and (max-width: 425px) {
  .App-setting-bar {
    padding: 10px;
  }

  .App-button-icon {
    width: 18px;
    height: 18px;
  }
}
.Joke {
  position: relative;
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
  min-width: 320px;
  text-align: center;
  color: var(--primary-text-color);
  margin: auto;
  padding: 15px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media only screen and (max-width: 425px) {
  .Joke {
    padding-top: 40px;
  }
}
.Spinner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(236, 239, 241, 0.75);
  border-radius: 5px;
}

.Spinner-icon {
  -webkit-animation: Spinner-icon-spin infinite 1.25s linear;
          animation: Spinner-icon-spin infinite 1.25s linear;
  width: 48px;
  height: 48px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@-webkit-keyframes Spinner-icon-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Spinner-icon-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
:root {
  --modal-bg-color: #ffffff;
  --modal-padding: 12px;
}

.Modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.35);
}

.Modal.open {
  display: block;
}

.Modal.close {
  display: none;
}

.Modal-content {
  position: fixed;
  background: #ffffff;
  background: var(--modal-bg-color);
  width: 30%;
  height: auto;
  top: 40%;
  left: 50%;
  border-radius: 5px;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}

.Modal-content-header {
  position: relative;
  padding: 12px;
  padding: var(--modal-padding);
  height: 46px;
  border-bottom: 1px solid #e0e0e0;
}

.Modal-content-header span {
  font-size: 1rem;
  font-weight: bold;
}

.Modal-content-body {
  padding: calc(2 * 12px) 12px;
  padding: calc(2 * var(--modal-padding)) var(--modal-padding);
}

.Modal-close {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  top: 12px;
  top: var(--modal-padding);
  right: 12px;
  right: var(--modal-padding);
}

.Modal-close:hover {
  cursor: pointer;
}

.Modal-close-icon {
  width: 22px;
  height: 22px;
}

@media only screen and (max-width: 425px) {
  .Modal-content {
    width: 80%;
  }
}

@media (min-width: 425.2px) and (max-width: 768px) {
  .Modal-content {
    width: 60%;
  }
}

@media (min-width: 768.2px) and (max-width: 992px) {
  .Modal-content {
    width: 50%;
  }
}
